import Link from 'next/link';

export default function Page() {
  return (
    <div className="flex flex-col min-h-screen bg-[var(--background)] text-[var(--foreground)] relative overflow-x-hidden">
      {/* 祥云装饰 */}
      <div className="chinese-cloud" style={{top: 24, left: 32}}>
        <svg viewBox="0 0 80 40" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M10 30 Q 20 10, 40 20 T 70 25" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
      <main className="flex-grow container mx-auto px-4 py-12 z-10">
        <div className="relative">
          <Link href="/" className="absolute -top-8 left-0 flex items-center gap-2 text-[var(--border)] hover:text-[var(--accent)] transition-colors font-bold text-xl" style={{fontFamily: 'var(--font-chinese)'}}>
            <span className="text-2xl">←</span>
            <span>返回首页</span>
          </Link>
        </div>
        <header className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold mb-4 text-[var(--border)]" style={{fontFamily: 'var(--font-chinese)'}}>案例演示：CSS样式的三种引入方式</h1>
          <p className="text-lg text-[var(--accent)] max-w-3xl mx-auto" style={{fontFamily: 'var(--font-chinese)'}}>
            本案例展示了在HTML中应用CSS的三种基本方法：外部样式表、内部样式表和内联（或称内嵌）样式。
          </p>
        </header>
        <div className="chinese-border bg-black/70">
          <div className="p-6 border-b border-[var(--border)]">
            <h3 className="text-xl font-semibold text-[var(--border)]" style={{fontFamily: 'var(--font-chinese)'}}>效果演示</h3>
          </div>
          <div className="p-8 space-y-6">
            <p className="text-[var(--accent)]" style={{fontFamily: 'var(--font-chinese)'}}>
              下列表中的每一项都由不同方式定义样式：
            </p>
            <div className="p-6 bg-black/80 chinese-border">
              <ul className="list-disc list-inside space-y-4 text-lg" style={{fontFamily: 'var(--font-chinese)'}}>
                <li className="text-[var(--accent)]">
                  <span className="font-semibold">[外部样式表]:</span> 本行文本的样式（如基础颜色、字体）可能由全局 `globals.css` 文件定义。
                </li>
                <li className="underline decoration-wavy text-[var(--border)]">
                  <span className="font-semibold">[内部样式表]:</span> 本行文本的样式（如下划线和颜色）通过页面级样式模拟（在React/Next.js中通常通过CSS-in-JS或模块化CSS实现，此处用Tailwind类模拟）。
                </li>
                <li style={{ color: '#bfa76a', fontWeight: 'bold' }}>
                  <span style={{ color: '#e7b85a', textDecoration: 'none' }}>[内联样式]:</span> 本行文本的样式（如金色和粗体）直接通过 `style` 属性定义，具有最高优先级。
                </li>
              </ul>
            </div>
          </div>
        </div>
      </main>
      {/* 古建筑底部装饰 */}
      <div className="chinese-building">
        <svg viewBox="0 0 220 90" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="30" y="60" width="160" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="3"/>
          <path d="M30 60 Q 110 10, 190 60" stroke="#e7b85a" strokeWidth="5" fill="none"/>
          <rect x="80" y="40" width="60" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="2"/>
          <path d="M80 40 Q 110 20, 140 40" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
    </div>
  );
} 